<template>
  <div class="article-container">
    <el-card class="top-card">
      <div slot="header" class="clearfix">
        <!--  -->
        <breadcrumb></breadcrumb>
      </div>
      <!-- form -->
      <article-form
        :dianji="dianji"
        @status="statusClick"
        :channels="channels"
        :loading="loading"
      ></article-form>
    </el-card>
    <el-card class="bottom-card">
      <div slot="header">{{ totalCount }}:</div>
      <!--  -->
      <article-table
        class="table-card"
        :articles="articles"
        :loading="loading"
        @articleId="articleId"
      ></article-table>
      <!--  -->
      <article-pagination
        @onCurrentChange="onCurrentChange"
        :totalCount="totalCount"
        :pageSize="pageSize"
        :loading="loading"
      ></article-pagination>
    </el-card>
  </div>
</template>

<script>
import Breadcrumb from './compschild/Breadcrumb'
import ArticleForm from './compschild/Form'
import ArticleTable from './compschild/Table'
import ArticlePagination from './compschild/Pagination'

// 
import { getArticles, getArticleChannels, articleId } from '@/api/article'

export default {
  name: 'ArticleIndex',
  components: {
    Breadcrumb,
    ArticleForm,
    ArticleTable,
    ArticlePagination,
  },
  props: {},
  data() {
    return {
      articles: [], // 
      totalCount: 0, // 
      pageSize: 10, // 
      status: null, //   
      dianji: null, //   
      channels: [], // 
      channelId: null, // 
      rangDate: null, // 
      loading: true, //  loading
      page: 1, // 
    }
  },
  computed: {},
  watch: {},
  created() {
    // 
    this._getArticles()
    this._getArticleChannels()

    //  
    //  
    this.dianji = this._getArticles
  },
  mounted() {},
  methods: {
    // 
    // page = 1 
    _getArticles(page = 1) {
      //   loading
      this.loading = true
      getArticles({
        page,
        per_page: this.pageSize,
        status: this.status,
        channel_id: this.channelId ? this.channelId : null,
        begin_pubdate: this.rangDate ? this.rangDate[0] : null, // 
        end_pubdate: this.rangDate ? this.rangDate[1] : null, // 
      }).then((res) => {
        // console.log(res)
        const { results, total_count } = res.data.data
        this.articles = results
        this.totalCount = total_count

        //   loading
        this.loading = false
      })
    },

    //  
    onCurrentChange(page) {
      //    data
      this._getArticles(page)
      this.page = page
    },

    statusClick(status, region, rangDate) {
      this.status = status
      // console.log(status)
      this.channelId = region
      // console.log(region)
      this.rangDate = rangDate
      // console.log(rangDate)
    },

    //  
    _getArticleChannels() {
      getArticleChannels().then((res) => {
        // console.log(res)
        this.channels = res.data.data.channels
      })
    },

    // 
    articleId(id) {
      // console.log(id)
      // console.log(id.toString())
      //    
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
      })
        .then(() => {
          // 
          // console.log(id)
          articleId(id.toString()).then((res) => {
            console.log(res)
            // 
            this._getArticles(this.page)
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '',
          })
        })
    },
  },
}
</script>

<style scoped lang="less">
.top-card {
  margin-bottom: 20px;
}
.bottom-card {
  .table-card {
    margin-bottom: 20px;
  }
}
</style>
