<template>
  <el-form
    ref="publish-form"
    :model="article"
    :rules="publishRules"
    label-width="60px"
  >
    <el-form-item label="" prop="title">
      <el-input v-model="article.title"></el-input>
    </el-form-item>
    <el-form-item label="" prop="content">
      <quill-editor
        v-model="article.content"
        ref="myQuillEditor"
        style="height: 100%"
        :options="editorOption"
      >
      </quill-editor>
    </el-form-item>
    <el-form-item label="">
      <el-radio-group
        v-model="article.cover.type"
        @change="radioChange(article)"
      >
        <el-radio :label="1"></el-radio>
        <el-radio :label="3"></el-radio>
        <el-radio :label="0"></el-radio>
        <el-radio :label="-1"></el-radio>
      </el-radio-group>
      <!--  -->
      <slot></slot>
    </el-form-item>
    <el-form-item label="" prop="channel_id">
      <el-select v-model="article.channel_id" placeholder="">
        <el-option
          :label="item.name"
          :value="item.id"
          v-for="(item, index) in channels"
          :key="index"
        ></el-option>
      </el-select>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="onPublish(article, false)">{{
        activeId ? '' : ''
      }}</el-button>
      <el-button
        v-if="activeId ? false : true"
        @click="onPublish(article, true)"
        ></el-button
      >
    </el-form-item>
  </el-form>
</template>

<script>
// 
import { quillEditor } from 'vue-quill-editor'
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
//   
import { ImageDrop } from 'quill-image-drop-module'
import ImageResize from 'quill-image-resize-module'
Quill.register('modules/imageDrop', ImageDrop)
Quill.register('modules/imageResize', ImageResize)

// base64img
// formData

// 
import globalBus from '@/utils/global-bus'

export default {
  name: 'PublishForm',
  components: { quillEditor },
  props: {
    channels: {
      type: Array,
      defalut() {
        return []
      },
    },
    activeId: {
      type: String,
      defalut: '',
    },
    index: {
      type: Number,
      defalut: 0,
    },
    url: {
      type: String,
      defalut: '',
    },
  },
  data() {
    return {
      article: {
        title: '', // 
        content: '', // 
        // 
        cover: {
          type: 0, //  -1:0-1-13-3
          images: [], // 
        },
        channel_id: null, // id
      },
      publishRules: {
        title: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 5,
            max: 30,
            message: ' 5  30 ',
            trigger: 'blur',
          },
        ],
        content: [
          {
            validator(rule, value, callback) {
              // value  
              if (value === '<p></p>') {
                callback(new Error(''))
              } else {
                callback()
              }
            },
            trigger: 'blur',
          },
          { required: true, message: '', trigger: 'blur' },
        ],
        channel_id: [{ required: true, message: '' }],
      },
      // 
      editorOption: {
        modules: {
          // imageDrop: true, //
          // //
          // imageResize: {
          //   displaySize: true,
          // },
          imageDrop: true, //
          //
          imageResize: {
            displayStyles: {
              backgroundColor: 'black',
              border: 'none',
              color: 'white',
            },
            modules: ['Resize', 'DisplaySize', 'Toolbar'],
          },
          toolbar: [
            ['bold', 'italic', 'underline', 'strike'], //    
            ['blockquote', 'code-block'], //   
            [{ header: 1 }, { header: 2 }], // 12 
            [{ list: 'ordered' }, { list: 'bullet' }], // 
            [{ script: 'sub' }, { script: 'super' }], // /
            [{ indent: '-1' }, { indent: '+1' }], // 
            // [{'direction': 'rtl'}],                         // 
            [{ size: ['small', false, 'large', 'huge'] }], // 
            [{ header: [1, 2, 3, 4, 5, 6, false] }], // 
            [{ color: [] }, { background: [] }], // 
            [{ font: [] }], // 
            [{ align: [] }], // 
            ['clean'], // 
            ['link', 'image', 'video'], // 
          ], //
        },
        placeholder: '', //
        readyOnly: false, //
        theme: 'snow', // snow/bubble
        syntax: true, //
      },
      indexImg: null,
      urlImg: '',
    }
  },
  computed: {},
  watch: {
    index(value) {
      this.indexImg = value
    },
    url(value) {
      this.urlImg = value
    },
  },
  created() {},
  mounted() {},
  methods: {
    onPublish(article, draft) {
      this.article.cover.images[this.indexImg] = this.urlImg

      globalBus.$emit('indexUrl', this.article.cover.images[this.indexImg])
      // 
      this.$refs['publish-form'].validate((valid) => {
        // valid  false   true
        // console.log(valid)
        if (!valid) {
          return
        } else {
          //  true 
          this.$emit('addArticle', article, draft)
        }
      })
    },

    // 
    radioChange(article) {
      this.$emit('radioChange', article.cover.type)
      globalBus.$emit('radioChange', article.cover.type)
    },
  },
}
</script>

<style scoped lang="less">
</style>


