<template>
  <div>
    <div class="upload-cover" @click="imageCover">
      <div class="type">
        <el-image
          ref="image"
          class="cover-img"
          :src="srcImg"
          @click.native="imageCover"
        >
          <div slot="error" class="image-slot">
            <i class="el-icon-picture-outline i"></i>
          </div>
        </el-image>
      </div>
    </div>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisible" append-to-body>
      <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
        <el-tab-pane label="" name="first">
          <!--  -->
          <image-layout
            ref="image-layout"
            :images="images"
            :loadImage="loadImage"
            :is-show-add="false"
            :is-show-action="false"
            is-show-selected
            @collect="collect"
          ></image-layout>

          <!--  -->
          <image-pagination
            @changePage="changePage"
            :totalCount="totalCount"
            :pageSize="pageSize"
            :currentpagesync="currentpagesync"
          ></image-pagination>
        </el-tab-pane>
        <el-tab-pane label="" name="second">
          <input ref="input" type="file" @change="onChangeInput" />
          <img ref="img" src="" alt="" />
        </el-tab-pane>
      </el-tabs>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="onCoverConfirm"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import ImageLayout from '@/views/image/compschild/ImageLayout'
import ImagePagination from '@/views/image/compschild/ImagePagination'

// 
import globalBus from '@/utils/global-bus'

// 
import { uploadImage, getImages } from '@/api/image'

export default {
  name: '',
  components: {
    ImageLayout,
    ImagePagination,
  },
  props: {},
  data() {
    return {
      type: null,
      dialogVisible: false,
      activeName: 'first',
      srcImg: '',
      images: [], // 
      loadImage: null, // 
      page: 1, // 
      totalCount: 0, // 
      pageSize: 12, // 
      currentpagesync: 1, //  
    }
  },
  computed: {},
  watch: {},
  created() {
    globalBus.$on('radioChange', (type) => {
      this.type = type
    })
    globalBus.$on('indexUrl', (img) => {
      this.srcImg = img
    })

    // 
    this.loadImage = this.loadImages
    this.loadImages(this.page)
  },
  mounted() {},
  methods: {
    imageCover() {
      this.dialogVisible = true
    },

    handleClick(tab, event) {
      // console.log(tab, event)
    },

    onChangeInput() {
      // 
      const file = this.$refs.input.files[0]

      // 
      this.$refs.img.src = window.URL.createObjectURL(file)
    },

    onCoverConfirm() {
      //  tab  input-file 
      const file = this.$refs.input.files[0]
      if (this.activeName === 'second') {
        if (!file) {
          this.$message({
            type: 'warning',
            message: '',
          })
          return
        }

        // 
        this.dialogVisible = false

        // 
        const fd = new FormData()
        fd.append('image', file)
        // 
        uploadImage(fd).then((res) => {
          this.srcImg = res.data.data.url

          this.$emit('coverUpload', res.data.data.url)
        })
      } else if (this.activeName === 'first') {
        // 
        this.dialogVisible = false
        //
        const selected = this.$refs['image-layout'].selected
        if (selected === null) {
          this.$message({
            type: 'warning',
            message: '',
          })
        }
        this.srcImg = this.images[selected].url
        this.$emit('coverUpload', this.images[selected].url)
      }
    },

    // 
    loadImages(page = 1, value = false) {
      // 
      this.currentpagesync = page
      getImages({
        collect: value,
        page,
        per_page: this.pageSize,
      }).then((res) => {
        console.log(res)
        const { data } = res.data
        this.images = data.results
        this.totalCount = data.total_count // 
      })
    },

    // 
    collect(value) {
      // 
      this.loadImages(1, value)
    },

    // 
    changePage(page) {
      this.page = page
      this.loadImages(this.page)
    },
  },
}
</script>

<style scoped lang="less">
.upload-cover {
  display: flex;
  .type {
    margin-right: 10px;
    width: 150px;
    height: 120px;
    border: 1px dotted #ccc;
    .cover-img {
      text-align: center;
      line-height: 120px;
      width: 100%;
      height: 100%;
      .i {
        font-size: 30px;
        color: #ccc;
      }
    }
  }
}
</style>