<template>
  <div class="publish-container">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <!--  -->
        <publish-breadcrumb :activeId="activeId"></publish-breadcrumb>
      </div>
      <!--  -->
      <publish-form
        ref="publishForm"
        :channels="channels"
        :activeId="activeId"
        :index="index"
        :url="url"
        @addArticle="addArticle"
        @radioChange="radioChange"
      >
        <!--  -->
        <div v-if="type > 0" class="upload-cover">
          <upload-cover
            v-for="(item, index) in type"
            :key="item"
            @coverUpload="coverUpload(index, $event)"
          />
        </div>
      </publish-form>
    </el-card>
  </div>
</template>

<script>
import PublishBreadcrumb from './compschild/PublishBreadcrumb'
import PublishForm from './compschild/PublishForm'
import UploadCover from './compschild/UploadCover'

// 
import {
  getArticleChannels,
  addArticle,
  getArticle,
  updataArticle,
} from '@/api/article'

export default {
  name: 'PublishIndex',
  components: {
    PublishBreadcrumb,
    PublishForm,
    UploadCover,
  },
  props: {},
  data() {
    return {
      channels: [], // 
      activeId: null, // id
      type: null,
      index: null,
      url: '',
    }
  },
  computed: {},
  watch: {},
  created() {
    // 
    this._getArticleChannels()

    // id
    this.activeId = this.$route.query.id

    // 
    // 
    // id
    if (this.activeId) {
      this.loadArticle()
    }
  },
  mounted() {},
  methods: {
    _getArticleChannels() {
      getArticleChannels().then((res) => {
        const { data } = res.data
        this.channels = data.channels
      })
    },

    addArticle(article, draft) {
      // 
      if (this.activeId) {
        // 
        updataArticle(
          this.activeId,
          this.$refs.publishForm.article,
          (draft = false)
        ).then((res) => {
          this.$message({
            message: '',
            type: 'success',
          })
          // 
          this.$router.push('/article')
        })
      } else {
        // 
        addArticle(article, draft).then((res) => {
          this.$message({
            message: draft ? '' : '',
            type: 'success',
          })
          // 
          this.$router.push('/article')
        })
      }
    },

    // 
    loadArticle() {
      // 
      getArticle(this.activeId).then((res) => {
        this.$refs.publishForm.article = res.data.data
      })
    },

    radioChange(type) {
      this.type = type
    },

    coverUpload(index, url) {
      this.index = index
      this.url = url
    },
  },
}
</script>

<style scoped lang="less">
.upload-cover {
  display: flex;
}
</style>
