<template>
  <div class="">
    <div class="action-head" style="padding-bottom: 20px">
      <!-- @change	(value) Radio label  -->
      <!--   -->
      <el-radio-group v-model="collect" size="small" @change="onCollect">
        <el-radio-button :label="false"></el-radio-button>
        <el-radio-button :label="true"></el-radio-button>
      </el-radio-group>
      <el-button
        v-if="isShowAdd"
        type="success"
        size="small"
        @click="dialogUpLoadVisible = true"
        ><i class="el-icon-upload el-icon--right"></i
      ></el-button>
    </div>
    <el-row :gutter="20">
      <el-col
        class="image-item"
        :xl="4"
        :lg="4"
        :md="6"
        :sm="8"
        :xs="12"
        :span="4"
        v-for="(item, index) in images"
        :key="index"
        @click.native="selected = index"
      >
        <el-image style="height: 200px" :src="item.url" fit="cover"></el-image>
        <!--  -->
        <div
          v-show="isShowSelected && selected === index"
          class="selected"
        ></div>
        <div class="scsc" v-if="isShowAction">
          <el-button
            class="image-button"
            type="warning"
            :icon="item.is_collected ? 'el-icon-star-on' : 'el-icon-star-off'"
            @click="isCollect(item)"
            circle
            size="mini"
          ></el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            circle
            size="mini"
            @click="ondetele(item.id)"
          ></el-button>
        </div>
      </el-col>
    </el-row>
    <el-dialog
      class="dialog"
      title=""
      :visible.sync="dialogUpLoadVisible"
      :append-to-body="true"
    >
      <!-- 
        upload
        :POST
        action 
        headers
       -->
      <el-upload
        drag
        action="http://api-toutiao-web.itheima.net/mp/v1_0/user/images"
        multiple
        :headers="upLoadHeader"
        name="image"
        :on-success="upLoadSuccess"
        :show-file-list="false"
      >
        <i class="el-icon-upload"></i>
        <div class="el-upload__text"><em></em></div>
        <div class="el-upload__tip" slot="tip">
          jpg/png500kb
        </div>
      </el-upload>
    </el-dialog>
  </div>
</template>

<script>
// 
import { collectImage, collectDelete } from '@/api/image'

export default {
  name: '',
  components: {},
  // props: ['images', 'loadImage'],
  props: {
    images: {
      type: Array,
      default() {
        return []
      },
    },
    loadImage: {
      type: Function,
      default() {
        return function () {}
      },
    },
    // 
    isShowAdd: {
      type: Boolean,
      default: true,
    },
    // 
    isShowAction: {
      type: Boolean,
      default: true,
    },
    // 
    isShowSelected: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    const user = JSON.parse(window.localStorage.getItem('user'))
    return {
      collect: false, //  
      dialogUpLoadVisible: false, // 
      upLoadHeader: {
        Authorization: `Bearer ${user.token}`,
      }, //
      selected: null, // 
    }
  },
  computed: {},
  watch: {
    // images(value) {
    //   this.loadingcollect = value
    //   // item  loading 
    //   // loading
    //   this.loadingcollect.forEach((item) => {
    //     item.loading = false
    //   })
    // },
  },
  created() {},
  mounted() {},
  methods: {
    onCollect(value) {
      this.$emit('collect', value)
      // console.log(value)  Radio label 
    },

    // 
    upLoadSuccess() {
      // 1 upload
      this.dialogUpLoadVisible = false
      // 2  
      // location.reload()
      //    
      this.loadImage()
      this.$message({
        type: 'success',
        message: '',
      })
    },

    // 
    isCollect(item) {
      // console.log(item)
      //  
      //  
      item.is_collected = !item.is_collected
      // if (item.is_collected) {
      //   item.is_collected = !item.is_collected
      // } else {
      //   item.is_collected = !item.is_collected
      // }

      collectImage(item.id, item.is_collected).then((res) => {
        console.log(res)

        if (res.data.data.collect === true) {
          this.$message({
            type: 'success',
            message: '',
          })
        } else {
          this.$message({
            type: 'warning',
            message: '',
          })
        }
        item.loading = false
      })
    },
    ondetele(id) {
      collectDelete(id).then((res) => {
        // 
        this.loadImage()
        this.$message({
          type: 'success',
          message: '',
        })
      })
    },
  },
}
</script>

