<template>
  <div class="">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <!--  -->
        <fans-breadcrumb></fans-breadcrumb>
      </div>
      <!--  -->
      <fans-list :fansList="fansList"></fans-list>

      <!--  -->
      <fans-pagination
        v-if="removelabel"
        :totalCount="totalCount"
        :pageSize="pageSize"
        @currentChange="currentChange"
      ></fans-pagination>
    </el-card>
  </div>
</template>

<script>
import FansBreadcrumb from './compschild/FansBreadcrumb'
import FansList from './compschild/FansList'
import FansPagination from './compschild/FansPagination'

// 
import globalBus from '@/utils/global-bus'

// 
import { fansList } from '@/api/fans'

export default {
  name: 'FansIndex',
  components: {
    FansBreadcrumb,
    FansList,
    FansPagination,
  },
  props: {},
  data() {
    return {
      label: '',
      removelabel: true,
      fansList: [], // 
      totalCount: 0,
      pageSize: 10,
    }
  },
  computed: {},
  watch: {},
  created() {
    globalBus.$on('fansLabel', (label) => {
      // console.log(label)
      this.label = label

      if (this.label === '') {
        this.removelabel = false
      } else {
        this.removelabel = true
      }
    })

    // 
    // this._fansStatisticsDate()
    this._fansList()
  },
  mounted() {},
  methods: {
    _fansList(page = 1) {
      fansList({
        page,
        per_page: this.pageSize,
      }).then((res) => {
        // console.log(res)
        const { results } = res.data.data
        this.fansList = results
        this.totalCount = res.data.data.total_count
      })
    },

    currentChange(page) {
      this._fansList(page)
    },
  },
}
</script>

<style scoped lang="less"></style>