import Vue from 'vue'
import VueRouter from 'vue-router'

import Login from '@/views/login/Index'
import Home from '@/views/home/Index'
import Layout from '@/views/layout/Index'
import Article from '@/views/article/Index'
import Publish from '@/views/publish/Index'
import Image from "@/views/image/Index"
import Commnet from "@/views/comment/Index"
import Settings from "@/views/settings/Index"
import Fans from "@/views/fans/Index";

Vue.use(VueRouter)

// 
const routes = [{
  path: '/login',
  name: 'login',
  component: Login
}, {
  path: '/',
  // 
  // name: 'layout',
  component: Layout,
  children: [{
    path: '', //  path   
    name: 'home',
    component: Home
  }, {
    path: '/article',
    name: 'article',
    component: Article
  }, {
    path: '/publish',
    name: 'publish',
    component: Publish
  }, {
    path: '/image',
    name: 'image',
    component: Image
  }, {
    path: '/comment',
    name: 'comment',
    component: Commnet
  }, {
    path: '/settings',
    name: 'settings',
    component: Settings
  }, {
    path: '/fans',
    name: 'fans',
    component: Fans
  }]
}]

const router = new VueRouter({
  routes,
  mode: 'history'
})

// 
// to
// from:
// next
// 
router.beforeEach((to, from, next) => {
  //   /login   /login  
  const user = JSON.parse(window.localStorage.getItem('user'))
    // 
  if (to.path !== '/login') {
    if (user) {
      //   
      next()
    } else {
      // 
      next('/login')
    }
  } else {
    // 
    next()
  }
})

export default router