<template>
  <div class="nav-header">
    <div>
      <i :class="zuoyou" @click="iClick"></i>
      <span class="left">f2e.xd</span>
    </div>
    <el-dropdown>
      <div class="right">
        <img :src="user.photo" alt="" />
        <span>
          {{ user.name }}<i class="el-icon-arrow-down el-icon--right"></i>
        </span>
      </div>
      <!-- <i class="el-icon-arrow-down el-icon--right"></i> -->
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item @click.native="onUserSettings"
          ></el-dropdown-item
        >
        <el-dropdown-item @click.native="onLogout"></el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
  </div>
</template>

<script>
// 
import { getUserProfile } from '@/api/user'

// 
import globalBus from '@/utils/global-bus'

export default {
  name: 'Header',
  components: {},
  props: {
    tupiao: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      user: {}, // 
    }
  },
  computed: {
    zuoyou() {
      return this.tupiao ? 'el-icon-s-unfold' : 'el-icon-s-fold'
    },
  },
  watch: {},
  created() {
    //  
    this._getUserProfile()

    //     
    globalBus.$on('update-user', (data) => {
      this.user.name = data.name
      this.user.photo = data.photo
    })
  },
  mounted() {},
  methods: {
    // 
    //   token
    _getUserProfile() {
      getUserProfile().then((res) => {
        this.user = res.data.data
      })
    },

    // 
    iClick() {
      // 
      this.$emit('iClick')
    },
    onLogout() {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
      })
        .then(() => {
          // token 
          window.localStorage.removeItem('user')
          // 
          this.$router.replace('/login')
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '',
          })
        })
    },

    onUserSettings() {
      this.$router.push('/settings')
    },
  },
}
</script>

