.nav-header {
  position: relative;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  .left {
    margin-left: 5px;
  }
  .right {
    img {
      margin-right: 5px;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      vertical-align: middle;
    }
    span {
      &:hover {
        cursor: pointer;
      }
    }
  }
}
</style>
