<template>
  <div class="image-container">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <!--  -->
        <image-breadcrumb></image-breadcrumb>
        <!-- <el-button style="float: right; padding: 3px 0" type="text"
          ></el-button
        > -->
      </div>

      <!--  -->
      <image-layout
        :images="images"
        :loadImage="loadImage"
        @collect="collect"
      ></image-layout>

      <!--  -->
      <image-pagination
        @changePage="changePage"
        :totalCount="totalCount"
        :pageSize="pageSize"
        :currentpagesync="currentpagesync"
      ></image-pagination>
    </el-card>
  </div>
</template>

<script>
import ImageBreadcrumb from './compschild/ImageBreadcrumb'
import ImageLayout from './compschild/ImageLayout'
import ImagePagination from './compschild/ImagePagination'

// 
import { getImages, collectImage } from '@/api/image'

export default {
  name: 'ImageIndex',
  components: {
    ImageBreadcrumb,
    ImageLayout,
    ImagePagination,
  },
  props: {},
  data() {
    return {
      images: [], // 
      loadImage: null, // 
      page: 1, // 
      totalCount: 0, // 
      pageSize: 12, // 
      currentpagesync: 1, //  
    }
  },
  computed: {},
  watch: {},
  created() {
    // 
    this.loadImage = this.loadImages
    this.loadImages(this.page)
  },
  mounted() {},
  methods: {
    // 
    loadImages(page = 1, value = false) {
      // 
      this.currentpagesync = page
      getImages({
        collect: value,
        page,
        per_page: this.pageSize,
      }).then((res) => {
        console.log(res)
        const { data } = res.data
        this.images = data.results
        this.totalCount = data.total_count // 
      })
    },

    // 
    collect(value) {
      // 
      this.loadImages(1, value)
    },

    // 
    changePage(page) {
      this.page = page
      this.loadImages(this.page)
    },
  },
}
</script>

<style scoped lang="less"></style>
