<template>
  <div class="">
    <el-row>
      <el-col :span="15" :xs="24">
        <el-form
          ref="userSettings"
          :model="userSettings"
          :rules="userSettings_form"
          label-width="80px"
        >
          <el-form-item label="">{{ userSettings.id }}</el-form-item>
          <el-form-item label="">{{ userSettings.mobile }}</el-form-item>
          <el-form-item label="" prop="name">
            <el-input v-model="userSettings.name"></el-input>
          </el-form-item>
          <el-form-item label="" prop="intro">
            <el-input type="textarea" v-model="userSettings.intro"></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input v-model="userSettings.email"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" :loading="loadingUpdate" @click="onSubmit"
              ></el-button
            >
          </el-form-item>
        </el-form>
      </el-col>
      <el-col :span="9">
        <div class="updataimage" @click="$refs.file.click()">
          <el-avatar
            shape="square"
            :size="150"
            fit="cover"
            :src="userSettings.photo"
          ></el-avatar>
          <p></p>
          <input type="file" hidden ref="file" @change="onChangeFile" />
        </div>
      </el-col>
    </el-row>
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      append-to-body
      @opened="onDialogOpened"
      @closed="onDialogClosed"
    >
      <div class="cropper_div">
        <img class="cropper_img" :src="blobData" ref="preview-image" />
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="onUpdateImg" :loading="loadingUpdate"
          > </el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
// 
import { upDataUser, upDateImg } from '@/api/user'

// 
import globalBus from '@/utils/global-bus'

// 
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'

export default {
  name: '',
  components: {},
  props: {
    user: {
      type: Object,
      defalut() {
        return {}
      },
    },
  },
  data() {
    return {
      userSettings: {
        email: '',
        id: null,
        intro: '',
        mobile: '',
        name: '',
        phtot: '',
      },
      dialogVisible: false, // 
      blobData: '', // 
      // 
      userSettings_form: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 7, message: ' 1  7 ', trigger: 'blur' },
        ],
        intro: [
          {
            validator(rule, value, callback) {
              if (value === '') {
                callback(new Error(''))
              } else {
                callback()
              }
            },
            trigger: 'blur',
          },
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          {
            pattern: /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/,
            message: '',
            trigger: 'blur',
          },
        ],
      },
      cropper: null, // 
      loadingUpdate: false, //  loading
    }
  },
  computed: {},
  watch: {
    user(value) {
      this.userSettings = value
    },
  },
  created() {},
  mounted() {},
  methods: {
    onSubmit() {
      //  loading
      this.loadingUpdate = true

      //   
      this.$refs['userSettings'].validate((val, object) => {
        if (!val) {
          return
        } else {
          // 
          upDataUser({
            // 
            // const {name,intro,email} = this.userSettings
            name: this.userSettings.name,
            intro: this.userSettings.intro,
            email: this.userSettings.email,
          }).then((res) => {
            //  
            // location.reload()
            //  loading
            this.loadingUpdate = false
          })

          //   
          globalBus.$emit('update-user', this.userSettings)

          this.$message({
            type: 'success',
            message: '',
          })
        }
      })
    },

    onChangeFile() {
      // 
      const file = this.$refs.file
      console.log(file.files[0]) // 
      const blobData = window.URL.createObjectURL(file.files[0])
      this.blobData = blobData

      // 
      this.dialogVisible = true

      //  change  value change 
      this.$refs.file.value = ''
    },

    onDialogOpened() {
      //  img 
      // : img 
      //   
      //   

      // 
      // 
      //   :  .replace
      //   : 
      // if (this.cropper) {
      //   // 
      //   this.cropper.replace(this.blobData)
      //   return
      // }
      // Vuedomref
      const image = this.$refs['preview-image']
      // const image = document.getElementById('image')
      this.cropper = new Cropper(image, {
        // 
        aspectRatio: 1,
        viewMode: 1,
        dragMode: 'none',
        aspectRatio: '1',
        cropBoxMovable: true,

        //  crop 
        crop(event) {
          // console.log(event.detail.x)
          // console.log(event.detail.y)
          // console.log(event.detail.width)
          // console.log(event.detail.height)
          // console.log(event.detail.rotate)
          // console.log(event.detail.scaleX)
          // console.log(event.detail.scaleY)
        },
      })
    },

    onDialogClosed() {
      //   
      this.cropper.destroy()
    },

    onUpdateImg() {
      // loading
      this.loadingUpdate = true
      // 
      this.cropper.getCroppedCanvas().toBlob((file) => {
        const fd = new FormData()
        fd.append('photo', file)
        // 
        upDateImg(fd).then((res) => {
          // 
          this.dialogVisible = false
          // 
          this.userSettings.photo = window.URL.createObjectURL(file) // api 

          // loading
          this.loadingUpdate = false

          this.$message({
            type: 'success',
            message: '',
          })

          //  
          globalBus.$emit('update-user', this.userSettings)
        })
      })
    },
  },
}
</script>

<style scoped lang="less">
.updataimage {
  text-align: center;
}
.cropper_div {
  .cropper_img {
    display: block;
    max-width: 100%;
    height: 200px;
  }
}
</style>
