<template>
  <div class="login-container">
    <!--
      el-form 
       el-form-item 
     -->
    <!--
       
       1  el-from  model 
       2  el-from-item  prop
         :prop 
       3  el-from  rules 

       :
       1  el-form  ref 
       2  ref  el-form  validate 
      -->
    <el-form
      class="login-form"
      ref="login-form"
      :model="user"
      :rules="rulesFrom"
    >
      <div class="xiaodeng"></div>
      <el-form-item prop="mobile">
        <el-input v-model="user.mobile" placeholder=""></el-input>
      </el-form-item>
      <el-form-item prop="code">
        <el-input v-model="user.code" placeholder=""></el-input>
      </el-form-item>
      <el-form-item prop="agree">
        <el-checkbox class="login-ckb" v-model="user.agree"
          ></el-checkbox
        >
      </el-form-item>
      <el-form-item>
        <el-button
          class="login-btn"
          type="primary"
          :loading="loginLoading"
          @click="onLogin"
          >{{ text }}</el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { login } from '@/api/user'

export default {
  name: 'LoginIndex',
  data() {
    return {
      user: {
        mobile: '13911111111', // 
        code: '246810', // 
        agree: false, // 
      },
      loginLoading: false, // loading
      text: '',
      rulesFrom: {
        // 
        // :  []
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          {
            pattern: /^1[3|5|6|7|8|9]\d{9}$/,
            message: '',
            trigger: 'blur',
          },
        ],
        code: [
          { required: true, message: '', trigger: 'blur' },
          {
            pattern: /^\d{6}$/,
            message: '',
            trigger: 'blur',
          },
        ],
        // 
        // :callback()
        // callbacknew Error('ggg')
        agree: [
          {
            validator: (rule, value, callback) => {
              if (value) {
                callback()
              }
              callback(new Error(''))
            },
            trigger: 'change',
          },
        ],
      },
    }
  },
  methods: {
    onLogin() {
      //  
      // const user = this.user

      // 
      // validate 
      this.$refs['login-form'].validate((val) => {
        //  
        if (!val) {
          return
        }
        //  
        this._login()
      })
    },
    _login() {
      this.loginLoading = true
      this.text = ''
      // 
      login(this.user)
        .then((result) => {
          // console.log(result)
          // 
          this.$message({
            message: '',
            type: 'success',
          })
          //  
          window.localStorage.setItem('user', JSON.stringify(result.data.data))

          this.loginLoading = false
          this.text = ''
          // 
          // this.$router.push('/')
          this.$router.push({ name: 'home' })
        })
        .catch(() => {
          // 
          this.$message.error('')
          this.loginLoading = false
          this.text = ''
        })
    },
  },
}
</script>

<style scoped lang="less">
.login-container {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: url('./login_bg.jpg') no-repeat;
  background-size: cover;

  .login-form {
    position: relative;
    width: 300px;
    padding: 50px;
    background-color: #fff;
    .xiaodeng {
      position: absolute;
      top: 15px;
      left: 150px;
      font-size: 18px;
      color: #2692ff;
    }
    .login-ckb {
      color: #a5a3a2;
    }
    .login-btn {
      width: 100%;
    }
  }
}
</style>
