<template>
  <div class="">
    <el-table :data="tableData" style="width: 100%" stripe>
      <el-table-column prop="title" label=""> </el-table-column>
      <el-table-column prop="total_comment_count" label="">
      </el-table-column>
      <el-table-column prop="fans_comment_count" label="">
      </el-table-column>
      <el-table-column prop="" label="">
        <template slot-scope="scope">
          <div>{{ scope.row.comment_status ? '' : '' }}</div>
        </template>
      </el-table-column>
      <el-table-column prop="" label="">
        <template slot-scope="scope">
          <el-switch
            v-model="scope.row.comment_status"
            active-color="#13ce66"
            inactive-color="#ff4949"
            @change="onSwitch(scope.row)"
            :disabled="scope.row.statusDisabled"
          >
          </el-switch>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
// 
import { updataCommentStatus } from '@/api/article'

export default {
  name: '',
  components: {},
  props: {
    comment: {
      type: Array,
      default() {
        return []
      },
    },
  },
  data() {
    return {
      tableData: [],
    }
  },
  computed: {},
  watch: {
    comment(value) {
      this.tableData = value
    },
  },
  created() {},
  mounted() {},
  methods: {
    // 
    onSwitch(article) {
      // 
      article.statusDisabled = true

      //  
      updataCommentStatus(article.id.toString(), article.comment_status).then(
        (res) => {
          // 
          article.statusDisabled = false
        }
      )
    },
  },
}
</script>

<style scoped lang="less"></style>
